.experince_details {
  position: relative;
}
.percent {
  position: relative;
  width: 80px;
  height: 80px;
}
.experince_details h4 {
  font-size: 0.9rem;
}
.percent svg {
  position: relative;
  width: 80px;
  height: 80px;
  transform: rotate(270deg);
}
.percent svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke-width: 4;
  stroke: #191919;
  transform: translate(5px, 5px);
}
.percent svg circle:nth-child(2) {
  stroke: var(--color-primary);
  stroke-dasharray: 220;
  stroke-dashoffset: calc(220 - (220 * 90) / 100);
}
.circle_two .percent svg circle:nth-child(2) {
  stroke: var(--color-primary);
  stroke-dasharray: 220;
  stroke-dashoffset: calc(220 - (220 * 85) / 100);
