.circle_title {
  text-align: center;
}
.circle_main {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dot {
  position: absolute;
  inset: 5px;
  z-index: 1;
  transform: rotate(calc(3.6deg * 90));
}
