.dot::before {
  content: '';
  position: absolute;
  top: -7px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background: var(--color-primary);
  box-shadow: 0 0 10px var(--color-primary), 0 0 30px var(--color-primary);
}
.number_circle {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.number_circle h2 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.number_circle h2 span {
  font-weight: 400;
  font-size: 1rem;
}
